html, body {
    height: 100%; /* 高さを100%に設定 */
    margin: 0; /* マージンをゼロに */
    background-color: wheat; /* 背景色を設定 */
  }
  
  body {
    padding-top: env(safe-area-inset-top); /* 上部のセーフエリアを考慮 */
    padding-left: env(safe-area-inset-left); /* 左側のセーフエリアを考慮 */
    padding-right: env(safe-area-inset-right); /* 右側のセーフエリアを考慮 */
    padding-bottom: env(safe-area-inset-bottom); /* 下部のセーフエリアを考慮 */
  }
body {
    padding: env(safe-area-inset); /* セーフエリアを考慮 */
    
  }
.animated-image {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
}

.letter-body {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 95%;
    height: 95%;
    background-color: white;
    border: 2px solid #80614d;
    scale: 0;
}


.letter-body-scale {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 95%;
    height: 95%;
    background-color: white;
    border: 2px solid #80614d;
    animation: fly-in-from-bottom 0.5s forwards;
}

.letter-body {
    opacity: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 95%;
    height: 95%;
    background-color: white;
    border: 2px solid #80614d;
}
.letter {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    animation: appear 0.5s forwards;
    animation-delay: 3.2s;
}

.open {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    animation: appear 0.5s forwards;
    animation-delay: 3.6s;
}

.letter-top {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    animation: appear 0.5s forwards;
    animation-delay: 3.2s;
}

.letter-open {
    position: absolute;
    width: 100%;
    height: 100%;
    transform-origin: 0 39%; 
    animation: letter-open 0.5s ease-in;
    animation-fill-mode: forwards;
}

