@keyframes fly-in-from-left {
    0% {
      transform: translateX(-100%); /* 右から飛び出す */
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: translateX(0); /* 最終位置 */
      opacity: 1;
    }
}

@keyframes fly-in-from-right {
    0% {
      transform: translateX(100%); /* 右から飛び出す */
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: translateX(0); /* 最終位置 */
      opacity: 1;
    }
}

@keyframes fly-in-from-top {
    0% {
      transform: translateY(-100%); /* 右から飛び出す */
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: translateY(0); /* 最終位置 */
      opacity: 1;
    }
}


@keyframes appear {
    0% {
    }
    100% {
        opacity: 1;
    }
}


@keyframes letter-open {
    0% {
    }
    100% {
        transform: rotateX(180deg);
    }
}


@keyframes fly-in-from-bottom {
    0% {
      transform: translateY(100%); 
      scale: 0;
      opacity: 0;
    }
    100% {
      transform: translateY(0); /* 最終位置 */
        top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
      opacity: 1;
      scale: 1;
    }
}

